body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.input-label{
    display: flex;
    flex-wrap: wrap;
}
.input-label input{
    width: 200px;
    border-radius: 2px;
}

.input-label label input{
    border: none;
    background: #f4f4f4;
    border: 1px solid #f4f4f4;
    outline: none;
    padding: 5px;
}

.input-label label input:hover{
    border-color: #bbbbbb;
}

.input-label label{
    border-color: #d5d5d5;
}

.input-label .name-text{
    display: inline-block;
    width: 150px;
    color: #deefef;
}
.input-label label{
    height: 7vh;
}
.input-label .error{
    color: red;
    display: inline-block;
    padding-left: 10px;
    width: 200px;
}
.full-dropdown{
    display: flex;
}

select, .goal-name{
    display: inline-block;
}

.goal-name{
    width: 150px;
    color: #deefef;
}

.full-dropdown select{
    width: 200px;

}

.full-dropdown .error{
    color: red;
    padding-left: 30px;
}
.submit-button{
    padding: 11px;
    color: #ffe4c4;
    /* font-family: Georgia, 'Times New Roman', Times, serif; */
    font-size: 1.2rem;
    margin-top: 5px;
    height: 40px;
    border-radius: 4px;
    background-color: rgb(16, 100, 16);
    outline: none;
    position: relative;
    right: 70px;
    border-radius: 10px;
    border: none;
}

.submit-button:hover{
    cursor: pointer;
    color: #ffecd5;
    background-color: rgb(34, 207, 34);
}

.submit-button:active{
    background-color: #aaaaaa;
    color: #ffe4c4;
}
.get-started{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.App-header{
    background: #282c34;
}
.results-info{
    color: rgb(245, 245, 245);
}
.App {
  text-align: center;
}

.icon {
  height: 50px;
  flex:1 1;
}

.welcome{
  text-align: center;
}
.getStartedButton{
  background-color: rgb(16, 100, 16);
  border-radius: 25px;
  padding: 15px;
  border: 15px;
  margin: 15px;
}
.wholeButton {
  text-decoration: none;
  color:black;
}

.getStartedButton:hover{
  background-color: rgb(34, 207, 34)
}

.imagee{
  height:100%;
  float: left;
  opacity: 0.5;
}
.imagee2{
  height: 100%;
  float: left;
}

.imagee:hover {
  opacity: 1;
}

/* .icon:hover {
  opacity: 1;
} */
.banner {
  padding: 15px;
  border: 15px;
  /* margin: 15px; */
  background-color: #585858;
  border-radius:10px;
  display: flex;
}

.App-logo {
  height: 40vmin;
}

.home {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

